@import url(https://fonts.googleapis.com/css2?family=Zilla+Slab:ital,wght@0,300;0,400;0,500;0,600;0,700;1,400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@400;500;700&display=swap);
html {
	margin: 0;
	padding: 0;
	font-size: 62.5%;
	background-color: #ccc;
}

body {
	margin: 0;
  padding: 0;
  font-family: 'Zilla Slab', serif;
}

.menuBtn {
	height: 1.8rem;
	margin-top: 3rem;
  cursor: pointer;
  padding: .7rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.line {
	height: .2rem;
	width: 1.5rem;
  background-color: #222;
  transition: all .2s;
  background-color: #782ec2;
}

.line-1-closed {
	width: 1.5rem;
	transition: all .2s;
}

.line-2-closed {
	width: 2rem;
	transition: all .2s;
}

.line-3-closed {
	width: 2.5rem;
	transition: all .2s;
}

.menuBtn:hover .line-1-closed, 
.menuBtn:hover .line-2-closed {
	width: 2.5rem;
}

.line-1-open {
	width: 2.5rem;
	transform: rotateZ(225deg);
	position: relative;
	top: 47%;
}

.line-2-open {
	display: none;
}

.line-3-open {
	width: 2.5rem;
	transform: rotateZ(-225deg);
	position: relative;
	top: -47%;
}

.sidebar {
	background-color: #112;
	width: 8rem;
	height: 100vh;
	position: fixed;
	top: 0;
	left: 0;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	z-index: 100;
	color: #d841d3;
	font-weight: 500;
}

.social-list {
	list-style-type: none;
	margin: 0;
	padding: 0;
	height: 20rem;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.social-links {
	font-size: 3rem;
}

.sidebar-date {
	font-size: 1.7rem;
	margin-bottom: 1rem;
}

.sidebar a:link {
	text-decoration: none;
	color: #d841d3;
}

.sidebar a:visited {
	text-decoration: none;
	color: #d841d3;
}

.sidebar a:hover {
	text-decoration: none;
	color: #ddd;
}

.sidebar a:active {
	text-decoration: none;
	color: #d841d3;
}

/* Responsive Styles */
@media screen and (max-width: 500px) {
	.sidebar {
		width: 5rem;
	}
}
.nav-menu {
	width: 40rem;
	height: 100vh;
	background-color: rgba(0, 0, 0, 0.9);
	position: fixed;
	top: 0;
	left: -32rem;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	transition: all .3s ease-in-out;
	box-shadow: .2rem .2rem 1rem rgba(0, 0, 0, .3);
	z-index: 50;
}

.nav-menu.open {
	left: 8rem;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.nav-list-container {
	width: 100%;
	height: 50%;
	margin-top: 12rem;
}

.nav-list {
	height: 100%;
	font-family: 'Noto Sans TC', sans-serif;
	font-size: 3.5rem;
	text-align: right;
	color: #ddd;
	list-style-type: none;
	margin: 0;
	padding-right: 5rem;
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
}

.navbar-link {
	text-decoration: none;
	color: #ddd;
	font-weight: 400;
	border-bottom: 3px solid transparent;
	transition: border-bottom .2s;
}

.navbar-link:link {
	color: #ddd;
}

.navbar-link:hover {
	border-bottom: 3px solid #d841d3;
}

.personal-info {
	color: #fff;
	font-size: 1.5rem;
	text-align: center;
	display: flex;
	flex-direction: column;
}

.personal-info span {
	margin-bottom: .4rem;
}

.navbar-link--active {
	color: #d841d3;
	border-bottom: 3px solid #d841d3;
	pointer-events: none;
}

/* Responsive Styles */
@media screen and (max-width: 500px) {
	.nav-menu {
		width: 30rem;
		left: -35rem;
	}

	.nav-menu.open {
		left: 5rem;
	}

	.nav-list {
		padding-right: 2rem;
	}
}

@media screen and (max-width: 350px) {
	.nav-menu {
		width: 25rem;
		left: -20rem;
	}

	.nav-list {
		font-size: 2.5rem;
	}
}

@media screen and (max-height: 500px) {
	.nav-list-container {
		margin-top: 5rem;
	}

	.nav-list {
		font-size: 3rem;
	}
}
